* {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  font-weight: 400;
  src: url(../../fonts/Roboto-Regular.ttf);
}

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  src: url(../../fonts/Roboto-Medium.ttf);
}

@font-face {
  font-family: "Roboto";
  font-weight: 700;
  src: url(../../fonts/Roboto-Bold.ttf);
}
